"use client"

import { useEffect, useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Plus, FileText } from "lucide-react"
import Link from "next/link"
import { taxesApi, type TaxRecord } from "@/lib/api/taxes"
import { toast } from "sonner"

export default function TaxesPage() {
  const [taxes, setTaxes] = useState<TaxRecord[]>([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    loadTaxes()
  }, [])

  const loadTaxes = async () => {
    try {
      const data = await taxesApi.getAll()
      setTaxes(data)
    } catch (error) {
      console.error("Failed to load taxes:", error)
      toast.error("Failed to load taxes")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="p-8 bg-muted/20 min-h-screen">
      <div className="max-w-6xl mx-auto space-y-8">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-foreground">Taxes</h1>
            <p className="text-muted-foreground mt-2">Manage vehicle taxes and documents</p>
          </div>
          <Button asChild>
            <Link href="/app/taxes/new" className="gap-2">
              <Plus size={18} />
              Add Tax Record
            </Link>
          </Button>
        </div>

        {/* Taxes Table */}
        <Card className="overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="border-b bg-muted/50">
                <tr>
                  <th className="text-left px-6 py-4 text-sm font-semibold text-foreground">Date</th>
                  <th className="text-left px-6 py-4 text-sm font-semibold text-foreground">Vehicle</th>
                  <th className="text-left px-6 py-4 text-sm font-semibold text-foreground">Type</th>
                  <th className="text-right px-6 py-4 text-sm font-semibold text-foreground">Amount</th>
                  <th className="text-left px-6 py-4 text-sm font-semibold text-foreground">Notes</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {taxes.length === 0 ? (
                  <tr>
                    <td colSpan={5} className="px-6 py-8 text-center text-muted-foreground">
                      No tax records found
                    </td>
                  </tr>
                ) : (
                  taxes.map((tax) => (
                    <tr key={tax.id} className="hover:bg-muted/50">
                      <td className="px-6 py-4 text-sm text-foreground">
                        {new Date(tax.date).toLocaleDateString()}
                      </td>
                      <td className="px-6 py-4 text-sm text-muted-foreground">
                        {tax.vehicle?.plate} ({tax.vehicle?.brand})
                      </td>
                      <td className="px-6 py-4 text-sm text-muted-foreground">{tax.type}</td>
                      <td className="px-6 py-4 text-sm font-medium text-foreground text-right">
                        ${Number(tax.amount).toFixed(2)}
                      </td>
                      <td className="px-6 py-4 text-sm text-muted-foreground">{tax.notes || "â€”"}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </Card>
      </div>
    </div>
  )
}
